<!-- Thema 3: Warum sind Port-UnabhÃ¤ngigkeit und Vollduplex-Kommunikation entscheidend fÃ¼r Switches? -->
<div class="border rounded p-3 bg-light mb-4">
  <p><strong>Thema:</strong><br />
    Warum sind Port-UnabhÃ¤ngigkeit und Vollduplex-Kommunikation entscheidend fÃ¼r Switches?
  </p>
  <hr />
  <p><strong>ãƒ†ãƒ¼ãƒ:</strong><br />
    ã‚¹ã‚¤ãƒƒãƒã«ãŠã‘ã‚‹ã€Œãƒãƒ¼ãƒˆã®ç‹¬ç«‹æ€§ã€ã¨ã€Œãƒ•ãƒ«ãƒ‡ãƒ¥ãƒ—ãƒ¬ãƒƒã‚¯ã‚¹é€šä¿¡ã€ã¯ãªãœé‡è¦ãªã®ã‹ï¼Ÿ
  </p>
</div>

<!-- è§£èª¬ãƒ–ãƒ­ãƒƒã‚¯ -->
<div class="row mb-4">
  <div class="col-md-6">
    <h5>ErklÃ¤rung (Deutsch)</h5>
    <p>
      Die hohe Effizienz moderner Switches beruht auf zwei zentralen Konzepten:
    </p>
    <ul>
      <li><strong>1. Port-UnabhÃ¤ngigkeit:</strong> Jeder Port kann unabhÃ¤ngig Daten senden und empfangen.</li>
      <li><strong>2. Vollduplex-Kommunikation:</strong> Innerhalb einer Verbindung kann gleichzeitig gesendet und empfangen werden.</li>
    </ul>
    <h6>ğŸ”¹ Port-UnabhÃ¤ngigkeit (Mehrere parallele Verbindungen)</h6>
    <p>
      Der Switch lernt durch MAC-Adressen-Tabellen, welche GerÃ¤te an welchen Ports angeschlossen sind.
      Basierend darauf kann er Frames gezielt nur an den Zielport senden.
    </p>
    <p>
      â¤ So kÃ¶nnen z.&nbsp;B. PC1 â†” PC2 und gleichzeitig PC3 â†” PC4 kommunizieren â€“ ohne sich gegenseitig zu stÃ¶ren.
    </p>
    <h6>ğŸ”¹ Vollduplex (Gleichzeitiges Senden und Empfangen)</h6>
    <p>
      Vollduplex ist mÃ¶glich, wenn beide GerÃ¤te sowie der Switch dies unterstÃ¼tzen.
      Die physikalische Voraussetzung: moderne Ethernet-Kabel (z.&nbsp;B. Cat5e) mit 4 Drahtpaaren.
    </p>
    <p>
      â¤ Zwei Paare fÃ¼r den Sendeweg, zwei Paare fÃ¼r den Empfang â€“ die Richtungen sind physikalisch getrennt.
    </p>
    <p>
      Die Switch-Ports enthalten dafÃ¼r spezialisierte Schaltungen (PHY/MAC), die gleichzeitige Verarbeitung ermÃ¶glichen.
    </p>

    <h6>â— Warum ist ein Hub immer Halbduplex?</h6>
    <p>
      Ein Hub arbeitet wie ein elektrischer Verteiler: Er sendet empfangene Signale an alle Ports weiter â€“ ohne Intelligenz.
      Da alle GerÃ¤te denselben Ãœbertragungskanal teilen, kÃ¶nnen nicht gleichzeitig Signale Ã¼bertragen werden.
    </p>
    <p>
      â¤ Sobald zwei GerÃ¤te gleichzeitig senden, kommt es zu einer <strong>Kollision</strong>.
      Deshalb funktioniert ein Hub nur im <strong>Halbduplex-Modus</strong> (entweder Senden oder Empfangen, nicht beides gleichzeitig).
    </p>
  </div>

